const BASE_URL = "http://localhost:3000"; // Update this to match your NestJS port

export const api = {
  async login(
    email: string,
    password: string,
  ): Promise<{ access_token: string }> {
    const response = await fetch(`${BASE_URL}/auth/login`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      credentials: "include", // Important for handling cookies if you're using them
      body: JSON.stringify({ email, password }),
    });

    if (!response.ok) {
      throw new Error("Login failed");
    }

    return response.json();
  },

  async signup(
    email: string,
    password: string,
  ): Promise<{ access_token: string }> {
    const response = await fetch(`${BASE_URL}/auth/register`, {
      // Updated to match typical NestJS auth endpoint
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      credentials: "include",
      body: JSON.stringify({ email, password }),
    });

    if (!response.ok) {
      throw new Error("Signup failed");
    }

    return response.json();
  },

  async getMenu(token: string): Promise<any[]> {
    const response = await fetch(`${BASE_URL}/menu`, {
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: "application/json",
      },
      credentials: "include",
    });

    if (!response.ok) {
      throw new Error("Failed to fetch menu");
    }

    return response.json();
  },
};

